<template>
    <div id="show-blog" v-theme:column="'wide'">
        <h1>博客总览</h1>
        <input type="text" placeholder="搜索" v-model="search">
        <div class="single-blog" v-for="blog in filtered">
            <router-link v-bind:to="'/blog/' + blog.id">
                <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
            </router-link>
            <article>
                {{blog.content | snippet}}
            </article>
        </div>
    </div>
</template>

<script>
    export default {
        name: "show-blog",
        data() {
            return {
                blogs:[],
                search: ""
            }
        },

        //搜索框
        computed:{
            filtered: function () {
                return this.blogs.filter((blog) => {
                    return blog.title.match(this.search);
                })
            }
        },
        created() {
            this.$http.get("https://wd1789916669mwgbtp.wilddogio.com/posts.json")
                .then(function (data) {
                    return (data.json());
                    console.log(data.json());
                })
                .then(function (data) {
                    var blogsArray=[];
                    for(let key in data){
                        console.log(data[key]);
                        data[key].id = key;
                        blogsArray.push(data[key]);
                    }
                    this.blogs = blogsArray;
                    console.log(this.blogs);
                })
        },
        //自定义过滤器   局部
        filters:{
            "to-uppercase":function (value) {
                return value.toUpperCase();  //转大写
            },
            "snippet":function (value) {
                return value.slice(1,85) + ".....";  //最多显示字符给数
            }
        },

    }
</script>

<style scoped>
    #show-blog {
        max-width:900px;
        margin: 0 auto;
    }

    .single-blog {
        padding: 20px;
        margin: 20px 0;
        box-sizing: border-box;
        background: #eee;
    }

    input[type="text"] {
        width: 80%;
        margin: 30px 0;
        padding: 10px;
        font-size: 16px;
        box-sizing: border-box;
    }
    #show-blog a{
        color: #444;
        text-decoration: none;
    }
</style>


