<template>
    <div id="add-blog">
        <h1>添加博客</h1>
        <from v-if="!subm">
            <lable>博客标题</lable>
            <input type="text" v-model="blog.title" required/>

            <lable>博客内容</lable>
            <textarea v-model="blog.content"></textarea>

            <lable>博客分类</lable>
            <div id="checkbox">
                <lable>Vue.js</lable>
                <input type="checkbox" value="Vue.js" v-model="blog.catges">
                <lable>Node.js</lable>
                <input type="checkbox" value="Node.js" v-model="blog.catges">
                <lable>React.js</lable>
                <input type="checkbox" value="React.js" v-model="blog.catges">
                <lable>Angular.js</lable>
                <input type="checkbox" value="Angular.js" v-model="blog.catges">
            </div>

            <lable>作者</lable>
            <select v-model="blog.author">
                <option v-for="author in authors">
                    {{author}}
                </option>
            </select>

            <button v-on:click="post">添加博客</button>
        </from>

        <div v-if="subm">
            <h3>你的博客发布成功！</h3>
        </div>

        <div id="preview" v-if="subm">
            <h3>博客总览</h3>
            <p>博客标题:</p>
            <p>{{blog.title}}</p>
            <p>博客类容:</p>
            <p>{{blog.content}}</p>
            <p>博客分类:</p>
            <ul v-for="catge in blog.catges">
                <li>
                    {{catge}}
                </li>
            </ul>
            <p>作者:</p>
            <p>{{blog.author}}</p>
        </div>
    </div>

</template>

<script>
    export default {
        name: "add-blog",
        data() {
            return {
                blog: {
                    title: "",
                    content: "",
                    catges: [],
                    author: ""
                },
                authors: [
                    "heaiah",
                    "dfssfa",
                    "sfadsf"
                ],
                subm: false,
            }
        },
        methods: {
            post:function () {
                this.$http.post("https://wd1789916669mwgbtp.wilddogio.com/posts.json",this.blog)
                    .then(function (data) {
                        console.log(data);
                        this.subm=true;
                    })
            }
        }
    }
</script>

<style scoped>
    #add-blog {
        margin: 0 auto;
        max-width: 550px;
        padding: 20px;
    }

    lable {
        display: block;
        margin: 30px 0 10px;
    }

    input[type="text"], textarea, select {
        display: block;
        width: 100%;
        padding: 6px;
    }

    textarea {
        height: 250px;
    }

    #checkbox lable {
        display: inline-block;
        margin-top: 0;
    }

    button {
        display: block;
        margin: 30px auto 0;
        background: crimson;
        color: white;
        border: 0;
        padding: 14px 30px;
        border-radius: 5px;
        font-size: 18px;
        cursor: pointer;
    }

    #preview {
        padding: 10px 20px;
        margin: 50px 0;
    }

    h3 {
        margin-top: 10px;
    }
</style>


