<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者:{{blog.author}}</p>
        <p>分类:</p>
        <ul>
            <li v-for="catge in blog.catges">
                {{catge}}
            </li>
        </ul>
        <button @click="deleteblog()">删除</button>
        <router-link :to="'/edit/' + id">编辑</router-link>
    </div>
</template>

<script>
    export default {
        name: "single-blog",
        data() {
            return {
                id: this.$route.params.id,
                blog: {}
            }
        },
        created() {
            this.$http.get('https://wd1789916669mwgbtp.wilddogio.com/posts/' + this.id + ".json")
                .then(function (data) {
                    // this.blog = data.body;
                    console.log(data);
                    return data.json();
                })
                .then(function (data) {
                    this.blog = data;
                })
        },
        methods:{
            deleteblog(){
                this.$http.delete('https://wd1789916669mwgbtp.wilddogio.com/posts/' + this.id + ".json")
                    .then(response =>{
                        this.$router.push({path:'/'})
                    })
            }
        }
    }
</script>

<style scoped>
    #single-blog {
        margin: 0 auto;
        max-width: 900px;
        padding: 20px;
        box-sizing: border-box;
        background: #eee;
    }
</style>


